body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
  font-family: 'Segoe UI', sans-serif;
}

.main {
  display: flex;
  gap: 20px;

}

.google-btn {
  width: fit-content;
  margin: 10px;
  align-self: center;
}

.posts {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.post-block {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.post {
  background-color: rgba(0, 0, 0, 0.199);
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 10px;
}

.post-title {
  color: white;
  font-weight: 700;
}

.post-body {
  text-align: justify;
  margin-left: 10px;
  color: rgb(57, 55, 55);

}

.post-link {
  color: black;
  text-decoration: none;
  font-size: 18px;
}

.comments {
  background-color: #0891d036;
  color: rgb(57, 55, 55);
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 10px;
}

.comments h2 {
  color: white;
  font-weight: bold;
}

.comment {
  margin-left: 10px;
}